body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(47, 44, 40, 0.5);
  color: rgb(233, 213, 196);
  background-image:
  url("./images/bgimage.jpg");
  height: 100vh;
  margin-top: -25px; 
  background-size: "cover";
  background-repeat:no-repeat;
  width: 70%;
  margin: auto;
  display: block;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
button {
  border: 1px solid rgb(255, 212, 172);
  border-radius: 5%;
  background-color: rgb(211, 198, 187);
  margin-left: 5px;
  margin-bottom: 10px;
}
button:active {
  background-color: rgb(179, 163, 149);
}
#addButton {
  margin-bottom: 30px;
  margin-top: 10px;  
  margin-left: 10px;
}
#submitForm {
  margin-top: 40px;   
}
label {
  margin-right: 10px;
  display: inline-block;
  width: 40px;
  text-align: right;
}
input {
  display: inline-block;
  width:180px;
  margin-top: 5px;
  outline: none;
  border: 1px solid rgb(172, 152, 115);
  color: rgb(59, 51, 42);
  background-color: rgba(227, 204, 192, 0.8);
}
input[type=text]:focus {
  background-color: rgb(227, 208, 188);
}
span {
  display: inline-block;
  width:100%;
  text-align: left;
  margin-left: -50px;
  margin-top: 5px;
}
.todoDiv {
  background-color: rgba(69, 63, 60, 0.0);
  width: 95%;
  margin-left: 60px; 
  text-align: left;
}
#todoButtons {  
  margin-top: 5px;
  margin-bottom: 10px;
  margin-left: 5px;
  text-align: left;
}
.todoDiv label{
  margin-left: -50px;
  display: flex;
}
.todoDiv input{
  width: 280px;
  width: 95%;
  margin-left: -50px;
}
.item {
  height: 80%;
  border-top: 1px solid rgb(152, 129, 86);
  background-color: rgba(69, 63, 60, 0.7);
}






